.sidebar {
  height: 100vh;
  flex: 0.2; /* <-- Flex uses remainder 20% of screen */
  background-color: #040404;
  color: white;
  padding-left: 10px;
  padding-right: 10px;
  min-width: 230px;
  height: 100vh; /* <-- uses up 100% of available view height */
}
.sidebar__logo {
  height: 70px;
  margin-right: auto;
  padding: 10px;
}
.sidebar__title {
  margin-left: 10px;
  padding: 5px;
  font-size: 12px;
}
